.main-container{
    padding-top: 110px;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-image: url(./assets/back.svg);
    margin: 0;
}
.text-container{
    text-align: center;
    padding: 95px 0 19px 0;
    height: 72px;
    width: 100%;
}
.number-container{
    text-align: center;
    padding: 19px 0 19px 0;
    height: 72px;
    width: 100%;
}
h1{
    color: #8d5fd3;
    margin: 0;
    font-size: 37px;
    font-family: Roboto, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.label{
    color: #8d5fd3;
    margin: 0;
    font-size: 27px;
    text-align: center;
    font-family: Roboto, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.number{
    color: #8d5fd3;
    margin: 0;
    font-size: 100px;
    font-family: Roboto, 'Open Sans', 'Helvetica Neue', sans-serif;
}
td{
    text-align: center;
}
.photo-container{
    text-align: center;
    height: 250px;
    width: 100%;
}
.photo{
    margin: 31px 0 31px 0;
}
.photo-min-container{
    text-align: center;
    height: 250px;
    width: 100%;
}
.photo-min{
    height: 109px;
    width: 109px;
    background-color: #d8c8f0;
    border-radius: 50%;
}
.heart{
    height: 78px;
    width: 78px;
}
.btn-container{
    height: 169px;
    width: 100%;
}
.but{
    padding-top: 17px;
    text-align: center;
    border-radius: 50px;
    margin: 10px 10% 2px 10%;
    height: 62px;
    width: 80%;
    background-color: #8d5fd3;
    color: #fff;
    font-size: 25px;
    font-family: Roboto, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.in-container{
    height: 120px;
    width: 100%;
}
.in{
    padding-top: 17px;
    text-align: center;
    border-radius: 50px;
    border: #fff 1px;
    margin: 10px 5% 2px 5%;
    height: 60px;
    width: 80%;
    background-color: #c2b4d8;
    color: #fff;
    font-size: 25px;
    font-family: Roboto, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.in:focus{
    outline: none;
}
.preloader{
    text-align: center;
    height: 250px;
    width: 100%;
}
.preloader__image_animate{
    height: 200px;
    transition: 1.47s linear;
    animation: up-down 1.47s infinite; /* Название анимации up-down */
}

@keyframes up-down{
    0%{
        top: calc(50% - var(--logo-half-size));
    }
    0%{
        top: calc(50% - 10px);
    }
    75%{
        top: calc(50%); 
    }
    100%{
        top: calc(50% - 10px);
    }
    from{
        -webkit-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to{
        -webkit-transform: rotate(360deg);
        -o-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}